    <div class="page">
        <div class="cont">
            <h3>获取课程收益</h3>
            <p class="text">我们需要您阅读药联课堂项目相关劳务协议。<br>
                请阅读后上传您的身份证照片，并提交电子签名。</p>
            <div class="dp">
                <div class="left">
                    <div class="img">
                        <img src="./img/live_sign_1@2x.png" alt="">
                    </div>
                    <div class="line"></div>
                    <div class="img">
                        <img src="./img/live_sign_2@2x.png" alt="">
                    </div>
                    <div class="line"></div>
                    <div class="img">
                        <img src="./img/live_sign_3@2x.png" alt="">
                    </div>
                    <div class="line"></div>
                    <div class="img">
                        <img src="./img/live_sign_4@2x.png" alt="">
                    </div>
                </div>
                <div class="right">
                    <div class="right-item">
                        <div>
                            <img src="./img/live_sign_read@2x.png" alt="">
                            <p>阅读药联课堂劳务协议</p>
                        </div>
                        <button @click="read()" v-if="item.step1.step1Status === '0'">去完成</button>
                        <img v-if="item.step1.step1Status === '1'" class="img2" src="./img/live_sign_complete@2x.png"
                             alt="">
                    </div>
                    <div class="right-item">
                        <div>
                            <img src="./img/live_sign_id@2x.png" alt="">
                            <p>上传身份证信息</p>
                        </div>
                        <button @click="id()" v-if="item.step2.step2Status === '0'">去完成</button>
                        <img class="img2" v-if="item.step2.step2Status === '1'" src="./img/live_sign_complete@2x.png"
                             alt="">
                    </div>
                    <div class="right-item">
                        <div>
                            <img src="./img/live_sign_card@2x.png" alt="">
                            <p>绑定银行卡</p>
                        </div>
                        <button @click="card()" v-if="item.step3.step3Status === '0'">去完成</button>
                        <img class="img2" v-if="item.step3.step3Status === '1'" src="./img/live_sign_complete@2x.png"
                             alt="">
                    </div>
                    <div class="right-item">
                        <div>
                            <img src="./img/live_sign_autograph@2x.png" alt="">
                            <p>签署电子签名</p>
                        </div>
                        <button @click="autograph()" v-if="item.step4.step4Status === '0'">去完成</button>
                        <img class="img2" v-if="item.step4.step4Status === '1'" src="./img/live_sign_complete@2x.png"
                             alt="">
                    </div>
                </div>
            </div>
            <div class="footer" v-if="item.getProfit === '0'">
                <button @click="protocolUpdate">获取收益</button>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "qianList",
        data() {
            return {
                item: {
                    step1:{
                        step1Status: null,
                    },
                    step2:{
                        step2Status: null,
                    },
                    step3:{
                        step3Status: null,
                    },
                    step4:{
                        step4Status: null,
                    },
                }
            }
        },
        created() {
            this.$api.protocolDetail({}).then((res) => {
                this.item = res
            })
        },
        destroyed() {
            console.log('页面离开了')
        },
        methods: {
            protocolUpdate() {
                if (this.item.step1.step1Status === '0') {
                    this.$toast('请阅读协议')
                    return false
                }
                if (this.item.step2.step2Status === '0') {
                    this.$toast('请上传身份证信息')
                    return false
                }
                if (this.item.step3.step3Status === '0') {
                    this.$toast('请绑定银行卡')
                    return false
                }
                if (this.item.step4.step4Status === '0') {
                    this.$toast('请签署电子签名')
                    return false
                }

                this.$api.protocolUpdate({
                    getProfit: '1'
                }).then(() => {
                    this.$toast('您已完成药联课堂相关劳务协议签署，我们将在1-7个工作日内打款')
                    history.back();
                })
            },
            read() {
                this.$router.push('read')
            },
            id() {
                this.$router.push('idPage')
            },
            card() {
                this.$router.push('card')
            },
            autograph() {
                this.$router.push('autograph')
            }
        }
    }
</script>

